import React, {Component} from 'react';



class Light extends Component{

    render(){
        if (this.props.state ==="0") {
            return (
                <div className='container justify-content-center'>
                    <h1 className='lightStatusText'> Hey!, the light is </h1>
                    <div className='row justify-content-center'>
                        <img src='/assets/images/on.png' className='stateImage '/>
                    </div>
                </div>
            );
        } else if(this.props.state ==="1") {
            return (
                <div className='container justify-content-center'>
                    <h1 className='lightStatusText'> Hey!, the light is </h1>
                    <div className='row justify-content-center'>
                        <img src='/assets/images/off.png' className='stateImage '/>
                    </div>
                </div>
            );
        }else{
            return(
                <div className='container justify-content-center'>
                    <h1 className='lightStatusText'> Error! call eyal or shaked </h1>
                    <h2 className='lightStatusText'>{this.props.state}</h2>
                    <div className='row justify-content-center'>
                        <img src='/assets/images/error.png' className='stateImage '/>
                    </div>
                </div>
            );

        }
    }
}

export default Light;